<template>
  <el-row class="index">
    <el-menu
        mode="horizontal"
        background-color="#545c64"
        router
        text-color="#fff"
        active-text-color="#ffd04b">
      <el-row class="headerTop">
        <el-image class="logo" src="img/logo.png"></el-image>
        <el-row class="nav">
          <el-menu-item index="index">主页</el-menu-item>
          <el-menu-item index="myself">简历</el-menu-item>
          <el-submenu index="home">
            <template slot="title">个人作品</template>
            <el-menu-item index="home">美文欣赏</el-menu-item>
            <el-menu-item index="joke">内涵段子</el-menu-item>
            <el-menu-item index="video">视频解析</el-menu-item>
          </el-submenu>
        </el-row>
      </el-row>
    </el-menu>
    <el-row class="swiper-outer">
      <transition-group name="list-complete" tag="div">
        <el-row class="swiper-inner list-complete-item" key="one" v-show="pagination===1">
          <el-row class="inner-text animated zoomIn" v-show="pagination===1">人生天地间，若白驹过隙，忽然而已</el-row>
        </el-row>
        <el-row class="swiper-inner list-complete-item" key="two" v-show="pagination===2">
          <el-row class="inner-text animated zoomIn" v-show="pagination===2">人生若只如初见，何事秋风悲画扇</el-row>
        </el-row>
        <el-row class="swiper-inner list-complete-item" key="three" v-show="pagination===3">
          <el-row class="inner-text animated zoomIn" v-show="pagination===3">为了避免结束，你避免了一切</el-row>
        </el-row>
      </transition-group>
      <el-row class="pagination" type="flex" justify="center">
        <el-row class="page"
                :class="pagination===1?'selected':''"
                @click.native="handleChangePagination(1)"></el-row>
        <el-row class="page"
                :class="pagination===2?'selected':''"
                @click.native="handleChangePagination(2)"></el-row>
        <el-row class="page"
                :class="pagination===3?'selected':''"
                @click.native="handleChangePagination(3)"></el-row>
      </el-row>
      <transition
          name="custom-classes-transition"
          enter-active-class="animated fadeIn"
          leave-active-class="animated fadeOut">
      </transition>
    </el-row>
    <el-row type="flex" justify="center" style="padding: 25px 0;">
      <el-image src="img/pic/3.png"></el-image>
    </el-row>
    <el-row class="scroll-outer">
      <el-row class="scroll-inner" type="flex" justify="center">
        <el-row class="scroll-text">
          <el-row>
            不求尽如人意,
          </el-row>
          <el-row>
            但求问心无愧
          </el-row>
        </el-row>
        <el-image ref="imgStyle1" :style="imgStyle1" class="scroll-img" src="img/pic/1.png"></el-image>
      </el-row>
      <el-row class="scroll-inner" type="flex" justify="center">
        <el-image ref="imgStyle2" :style="imgStyle2" class="scroll-img" src="img/pic/2.png"></el-image>
        <el-row class="scroll-text">
          <el-row>
            不畏将来,
          </el-row>
          <el-row>
            不念过往
          </el-row>
        </el-row>
      </el-row>
      <!--<el-row class="scroll-inner" type="flex" justify="center">-->
      <!--<el-image class="scroll-img" src="img/pic/pic_wechat.png"></el-image>-->
      <!--<el-row class="scroll-text">没有行动的梦想被称为妄想 ！</el-row>-->
      <!--</el-row>-->
    </el-row>
    <el-row class="footer-text">懒惰是你一生最大的敌人, 靠勤劳比靠侥幸的得来的收益更能致富。</el-row>
  </el-row>
</template>
<script>

export default {
  data () {
    return {
      pagination: 1,
      timer: null,
      imgStyle1: {
        transform: 'translate3D(30%,0,0)'
      },
      imgStyle2: {
        transform: 'translate3D(-10%,0,0)'
      },
    }
  },
  methods: {
    setTimer () {
      let _this = this
      this.timer = setInterval(function () {
        if (_this.pagination === 3) {
          _this.pagination = 1
        } else {
          _this.pagination++
        }
      }, 3000)
    },
    clearTimer () {
      if (this.timer) {
        clearInterval(this.timer)
      }
    },
    scrollAnimate () {
      this.imgStyle1.transform = `translate3d(${this.translate(this.$refs.imgStyle1.$el, 'left', 0.3)}%,0,0)`
      this.imgStyle2.transform = `translate3d(-${this.translate(this.$refs.imgStyle2.$el, 'left', 0.1)}%,0,0)`
    },
    translate (el, direction, num) {
      let h = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
      let scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
      let b = 0
      switch (direction) {
        case 'left':
          b = (h + scrollTop - el.offsetTop) / (el.offsetHeight + 100) * num
          break
        case 'right':
          break
        case 'top':
          break
        case 'bottom':
          break
      }
      if (b < 0) {
        b = 0
      } else if (b > num) {
        b = num
      }
      return (num - b) * 100
    },
    handleChangePagination (val) {
      this.clearTimer()
      this.setTimer()
      this.pagination = val
    },
  },
  mounted () {
    this.setTimer()
    window.addEventListener('scroll', this.scrollAnimate)
  },
  destroyed () {
    this.clearTimer()
    window.removeEventListener('scroll', this.scrollAnimate)
  }
}
</script>
<style lang="scss">
.index {
  position: static;
  width: 100%;
  overflow-x: hidden;

  .el-row {
    position: static;
  }

  .headerTop {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .logo {
      width: 60px;
      height: 60px;
      border-radius: 50%;
    }

    .nav {
      width: 75%;
      display: flex;
      justify-content: space-around;
    }
  }

  .list-complete-item {
    transition: all 1s;
    display: inline-block;
    margin-right: 10px;
  }

  .list-complete-enter, .list-complete-leave-to {
    opacity: 0;
    transform: translateX(0);
  }

  .list-complete-leave-active {
    position: absolute;
  }

  .swiper-outer {
    position: relative;
    overflow: hidden;
  }

  .pagination {
    position: absolute;
    left: 50%;
    bottom: 24px;
    z-index: 10001;
    border-radius: 15px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: -120px;

    .page {
      width: 80px;
      height: 15px;
      color: #333;
      font-family: "PingFangSC-Medium", "Microsoft YaHei", sans-serif;
      font-size: 18px;
      line-height: 40px;
      text-align: center;
      /*background-color: #fff;*/
      cursor: pointer;
      transition: .5s all;
    }

    .selected {
      /*background-color: #fff4ee;*/
      opacity: .6;
    }
  }

  .swiper-inner {
    width: 100%;
    height: 500px;
    background-position: center center;
    background-size: cover;
    line-height: 500px;
    text-align: center;
  }

  .swiper-inner:nth-of-type(1) {
    background-image: url('~@/../public/img/banner/b4-bg.png')
  }

  .swiper-inner:nth-of-type(2) {
    background-image: url('~@/../public/img/banner/b2-bg.png')
  }

  .swiper-inner:nth-of-type(3) {
    background-image: url('~@/../public/img/banner/b3-bg.png')
  }

  .inner-text {
    color: #fff;
    font-size: 60px;
    font-weight: 600;
    letter-spacing: 3px;
  }

  .scroll-inner {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 40px 0;
    background: linear-gradient(-30deg, #ffffff, #e6f1fd);
  }

  .scroll-text {
    color: #333;
    font-size: 30px;
    font-weight: 600;
    line-height: 45px;
    letter-spacing: 3px;
    margin-left: 25px;
  }

  .scroll-img {
    width: 426px;
    height: 364px;
    overflow: hidden;
    transform: translate3d(50%, 0, 0);
  }

  .footer-text {
    color: #fff;
    font-size: 30px;
    font-weight: 600;
    height: 300px;
    line-height: 300px;
    text-align: center;
    letter-spacing: 3px;
    /*background: linear-gradient(180deg, red, blue);*/
    background: linear-gradient(180deg, #ff7c33, #e95e21);
  }
}
</style>
